.cl {

    &--gray {
        color: $gray;
        flill: $gray;
        @include heading {
            color: $gray; } }

    &--lightgray {
        color: $lightGray;
        flill: $lightGray;
        @include heading {
            color: $lightGray; } }

    &--black {
        color: $black;
        fill: $black;
        @include heading {
            color: $black; } }
    &--green {
        color: $greenOPT;
        fill: $greenOPT;
        @include heading {
            color: $greenOPT; } }
    &--white {
        color: $white;
        fill: $white;
        @include heading {
            color: $white;
            fill: $white; } }
    &--silver {
        color: $silver;
        @include heading {
            color: $silver; } }
    &--blue {
        color: $blue;
        @include heading {
            color: $blue; } }
    &--darkBlue {
        color: $blue;
        @include heading {
            color: $blue; } } }
