.icon:before {
    content: "";
    display: inline-block;
    background-repeat: no-repeat;
    -webkit-background-size: contain;
    background-size: contain;
    background-position: 0 0; }
.icon {
    &_close:before {
        background-image: url("#{$gitPath}icon_close.svg");
        width: 26px;
        height: 26px;
        vertical-align: middle; }
    &_celebrate:before {
        background-image: url('#{$gitPath}txt_letscelebrate.png');
        width: 100%;
        height: 174px;
        @include MQ(S) {
            width: 416px; } }
    &_check:before {
        background: url("#{$gitPath}icon_check.png");
        width: 26px;
        height: 26px;
        vertical-align: middle; }
    &_logo:before {
        background: url("#{$gitPath}logo_opt.svg");
        background-repeat: no-repeat;
        -webkit-background-size: 80%;
        background-size: 80%;
        width: 197px;
        height: 57px;
        vertical-align: middle; } }

.icon_menu--svg {
    display: inline-block;
    height: 60px;
    width: 60px;
    margin: 1.25rem 1.25rem;
    filter: drop-shadow(0px 0px 2px rgba(0,0,0,0.5)); }
.deco {
    &_elipce {
        &:after {
            content: "";
            display: inline-block;
            background-repeat: no-repeat;
            -webkit-background-size: contain;
            background-size: contain;
            background-position: 0 0;
            background-image: url('#{$gitPath}/deco_elipce.png');
            width: 119px;
            height: 119px;
            position: relative;
            top: (-119px/2); } } }
