// text.sass
.title,
.subtitle,
.item,
.info,
.intro,
.acction,
.paragraf {
    display: block; }
.paragraf {
    &_em2 {
        p {
            @include font-size(1.5); } } }
//.tag_data
.txt {
    &_spacing {

        &--x02 {
            letter-spacing: 0.2rem; }

        &--x05 {
            letter-spacing: 0.5rem; }

        &--x1 {
            letter-spacing: 1rem; } } }

.txt {
    &_two {
        font-family: $second-font;
        letter-spacing: 0.2rem; }
    &_three {
        font-family: $three-font; }
    &_bold {
        font-weight: bold; }
    &_weight {
        &900 {
            font-weight: 900;
            @include header {
                font-weight: 900; }
            p {
                font-weight: 900; } } }
    &_italic {
        font-style: italic; }

    &_center {
        text-align: center; }
    &_upper {
        text-transform: uppercase; }
    &_capital {
        text-transform: capitalize; }
    &_right {
        text-align: right; }
    &_shadow {
        text-shadow: 0px 2px 2px ($black); }
    &_size {
        &--em1 {
            @include font-size(1.5);
            li,
            p,
            b,
            span {
                @include font-size(1.5); }
            @include heading {
                @include font-size(1.5); } }
        &--em2 {
            @include font-size(2);
            li,
            p,
            b,
            span {
                @include font-size(2); }
            @include heading {
                @include font-size(2); } }
        &--em2a {
            @include font-size(2.5);
            li,
            p,
            b,
            span {
                @include font-size(2.5); }
            @include heading {
                @include font-size(2.5); } }
        &--em3 {
            @include font-size(3);
            li,
            p,
            b,
            span {
                @include font-size(3); }
            @include heading {
                @include font-size(3); } }
        &--em4 {
            @include font-size(4);
            li,
            p,
            b,
            span {
                @include font-size(4); }
            @include heading {
                @include font-size(4); } }
        &--em5 {
            @include font-size(5);
            li,
            p,
            b,
            span {
                @include font-size(5); }
            @include heading {
                @include font-size(5); } }

        &--em6 {
            @include font-size(6);
            li,
            p,
            b,
            span {
                @include font-size(6); }
            @include heading {
                @include font-size(6); } }

        &--em7 {
            @include font-size(7);
            li,
            p,
            b,
            span {
                @include font-size(7); }
            @include heading {
                @include font-size(7); } } }

    &_completed {
        text-decoration: line-through;
        font-style: italic;
        color: $gainsboro; } }
.completado {
    text-decoration: line-through;
    font-style: italic;
    color: $gainsboro; }

.title {
    &_site {
        @include header {
            position: relative;
            display: inline-block;
            padding-bottom: 18px;
            &:after {
                position: absolute;
                content: '';
                left: 0px;
                bottom: 0px;
                width: 50px;
                height: 10px;
                background-color: $red;
                border-bottom: 5px solid $black; } }
        &.txt_center {
            @include header {
                &:after {
                    left: 50%;
                    margin-left: -25px; } } } } }
